






























































import {Component, Vue, Watch} from 'vue-property-decorator';
import {
    Field,
    Button,
    CellGroup,
    Icon,
    Col,
    Row,
    DropdownMenu,
    DropdownItem,
    RadioGroup,
    Radio,
    Toast,
    Overlay,
    Loading,
    NavBar,
    Picker
} from 'vant';
import {notes, NoteType} from '@/util/note';

import 'vant/lib/field/style';
import 'vant/lib/button/style';
import 'vant/lib/cell-group/style';
import 'vant/lib/icon/style';
import 'vant/lib/col/style';
import 'vant/lib/row/style';
import 'vant/lib/dropdown-item/style';
import 'vant/lib/dropdown-menu/style';
import 'vant/lib/radio/style';
import 'vant/lib/radio-group/style';
import 'vant/lib/toast/style';
import 'vant/lib/overlay/style';
import 'vant/lib/loading/style';
import 'vant/lib/nav-bar/style';
import 'vant/lib/picker/style';
import {AddressBaseModel, AuthOriginalTxModel, AuthAdminType} from '../../model/original_address_model';
import {DiamondUtil} from '../../util/diamond_util';
import {ToastUtil} from "../../util/toast";
import {CashboxJs} from '@/util/cashboxjs';
import {checkAddressInfo} from "../../util/utils";
import GlobalConfig from "../../config/global_config";

Vue.use(Field).use(Button).use(Col).use(Row).use(CellGroup).use(Icon).use(DropdownMenu).use(DropdownItem).use(Radio).use(RadioGroup).use(Toast).use(Overlay).use(Loading).use(NavBar).use(Picker);

@Component({})

export default class OrgInput extends Vue {
    private bodyHeight = 0;
    private inputName = '';
    private inputAddressValue = '';
    private middleTitleText = '';
    private rightTitleText = '';
    private isLoadingAccount = true;
    private isDisableInputAddress = false;
    private isDisableInputName = false;
    private isShowAddressPopup = false;
    private isShowRecord = false;
    private addressList = Array();
    private isDisableAddAdminRadio = false;
    private addressHintInfo = "";
    private AddAdmin = AuthAdminType.AddAdmin;
    private DeleteAdmin = AuthAdminType.DeleteAdmin;
    private radioAuthBtnValue = AuthAdminType.None;
    private SeparateSymbol = ";";
    private addressRightIcon = GlobalConfig.ICON_ARROW_DOWN;

    public created(): void {
        this.$store.state.title = this.$t('managerSetting');
        this.middleTitleText = this.$t('managerSetting').toString();
        this.rightTitleText = this.$t('authRecord').toString();
        this.bodyHeight = document.documentElement.clientHeight;
        {
            (window as any).nativeQrScanToJsResult = this.nativeQrScanToJsResult; // 注册 调用原生方法 返回调用方法
        }
    }

    public async mounted() {
        let columns: AddressBaseModel[] = [];
        try {
            //let nativeAddress = "5GrwvaEF5zXb26Fz9rcQpDWS57CtERHpNehXCPcNoHGKutQY";               //todo remove test account
            columns = await DiamondUtil.getInstance().loadOriginalList(this.$store.state.nativeChainAddress);
            this.isLoadingAccount = false;
        } catch (e) {
            this.isLoadingAccount = false;
            ToastUtil.fail("加载链上数据出现错误" + e.toString());
            return;
        }

        if (columns.length === 0) {
            ToastUtil.show("载入链上信息为空，请检查后重新尝试");
            return;
        }
        console.log("columns.length :" + columns.length);

        if (columns.length === GlobalConfig.MAX_ADMIN_COUNT) {    // 3个原始地址，只能改名字
            this.addressHintInfo = this.$t('original.inputAddress').toString();
            this.isDisableInputAddress = true;          // 设置不能输入地址
            this.isDisableInputName = true;
            this.isDisableAddAdminRadio = true;         // 不能再增加原始（管理员）了。设置radio不可选择。
            this.radioAuthBtnValue = this.DeleteAdmin;
            this.addressRightIcon = GlobalConfig.ICON_ARROW_DOWN;        // 选择图标
        } else {                                         // 少于3个原始地址，可添加，可修改
            this.addressHintInfo = "请选择或输入地址";
            this.isDisableInputAddress = false;
            this.radioAuthBtnValue = this.AddAdmin;
            this.addressRightIcon = GlobalConfig.ICON_SCAN;              // 扫描图标
        }

        this.$store.commit('emptyOriginalAddress');      // 先清空旧数据
        for (let i = 0; i < columns.length; i++) {
            const index = i;
            // todo 方式待优化，弹框显示效果，数据格式约定： 名称;地址
            this.addressList.push(columns[index].name + this.SeparateSymbol + columns[index].address);
            const addressModel = columns[index] as AddressBaseModel;
            this.$store.commit('addOriginalAddressModel', addressModel);
            if (columns[index].address.toLowerCase() === this.$store.state.nativeChainAddress.toLowerCase()) {  //判当前账户，是不是管理员
                this.isShowRecord = true;
            }
        }
    }

    public onClickBack() {
        this.$router.replace({name: 'cert_search'});
    }

    public onClickRight() {
        this.$router.push({name: 'auth_admin_record'});
    }

    public nextStep() {
        if (this.addressList.length === (GlobalConfig.MAX_ADMIN_COUNT - 1) && this.radioAuthBtnValue === this.DeleteAdmin) {
            ToastUtil.fail("提示：目前只有两个管理员，不能再删除了");
            return;
        }
        if ((this.radioAuthBtnValue === this.AddAdmin) || (this.radioAuthBtnValue === this.DeleteAdmin)) {
            if (this.inputAddressValue && this.inputName) {
                const authTx = new AuthOriginalTxModel();
                authTx.txName = this.inputName;
                authTx.txAddress = this.inputAddressValue;
                authTx.nowAdminAddress = this.$store.state.nativeChainAddress;
                console.log("entry page authTx.nowAdminAddress===>" + authTx.nowAdminAddress + "||" + this.$store.state.nativeChainAddress);
                if (this.radioAuthBtnValue === this.AddAdmin) {
                    authTx.authType = AuthAdminType.AddAdmin;
                } else {
                    authTx.authType = AuthAdminType.DeleteAdmin;
                }
                this.$store.commit('updateAdminModel', authTx);
                this.$router.push({name: 'original/confirm'});
            } else {
                ToastUtil.show(this.$t('notifyMsg.inputNotNull').toString());
            }
        } else {
            ToastUtil.show(this.$t('notifyMsg.choiceAuth').toString());
        }
    }

    public clickAddressInput() {
        if (this.AddAdmin === this.radioAuthBtnValue) {
            CashboxJs.getInstance().callNativeQrScanToJs();
        } else {
            this.isShowAddressPopup = true;
        }
    }

    public confirmPickerAddress(value: string): void {
        this.isShowAddressPopup = false;
        if (value) {
            let index = value.indexOf(this.SeparateSymbol);
            this.inputName = value.substring(0, index);
            this.inputAddressValue = value.substring(index + 1, value.length);
            const originalTx = new AuthOriginalTxModel();
            originalTx.txName = this.inputName;
            originalTx.txAddress = this.inputAddressValue;
        }
    }

    @Watch('radioAuthBtnValue', {immediate: true, deep: true})
    public Change(val: number) {
        if (this.$store.state.authOriginalTxModel.originalAddressModelList.length === GlobalConfig.MAX_ADMIN_COUNT) {    //有三个 原始（管理员）地址，不能再增加了
            this.isDisableAddAdminRadio = true;
            this.addressRightIcon = GlobalConfig.ICON_ARROW_DOWN;        //只能选,做删除操作
        } else {
            this.isDisableAddAdminRadio = false;
            if (val === this.AddAdmin) {
                this.addressRightIcon = GlobalConfig.ICON_SCAN;
            } else {
                this.addressRightIcon = GlobalConfig.ICON_ARROW_DOWN;
            }
        }
    }

    public nativeQrScanToJsResult(address: string) {
        this.inputAddressValue = "";
        this.inputAddressValue = address;
        if (!checkAddressInfo(address)) {
            ToastUtil.fail("不符合标准地址格式，请您再检查地址信息");
        }
    }
}
